@import url('input.css');
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: #F3F3F3;
}

.login-background{
	 background-image: url('./images/background.png');
	 background-repeat: no-repeat;
	 background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.copyright{
	background-image: url('./images/copyright.png');
	background-repeat: no-repeat;
	background-size: contain;
}

.game-banner{
	background-image: url('./images/games\ background.png');
	background-repeat: no-repeat;
	background-size: cover;
}

.loader-inner {
	width: 30px;
	height: 30px;
	display: inline-block;	
	position: relative;	
	margin-left: .7vw;
}
	.spinner {
		width: 100%;
		height: 100%;
     position: absolute;
     top: 0;
     left: 0%;
     border: solid 1px white;
     border-radius: 50%;
	}



	.blue, .white {
		width: 80%;
		height: 80%;
		border-radius: 50%;
		display: block;
		position: absolute;
		top: 10%;
		left: 10%;	
	}

	.blue { 
		background: #f79e1b;
		animation: blueloader 1.4s infinite linear;
	}
	.white { 
		background: #fff;
		animation: whiteloader 1.4s infinite linear;
	}
	.static {
		z-index: 100;
		transform: scale(1);
	}
	.active {
		z-index: 200;
		transform: scale(0.1);
	}

	.hide-spinner{
		width: 0;
		height: 0;
		margin: 0;
	}



/* // KEYFRAMES ------------------------------------------------------------- */
@keyframes spin {
	0%{
		transform: rotate(0deg);
	}
	100%{
		transform: rotate(359deg);
	}
}

@keyframes blueloader {
	0%{
		z-index: 100;
		transform: scale(1);
	}
	50%{
		z-index: 100;
		transform: scale(1);
	}
	51%{
		z-index: 200;
		transform: scale(0);
	}
	100%{
		z-index: 200;
		transform: scale(1);
	}
}

@keyframes whiteloader {
	0%{
		z-index: 200;
		transform: scale(0);
	}
	50%{
		z-index: 200;
		transform: scale(1);
	}
	51%{
		z-index: 100;
		transform: scale(1);
	}
	100%{
		z-index: 100;
		transform: scale(1);
	}
}